import Vue from 'vue';
import IconComponent from './IconComponent.vue';
export default Vue.extend({
    name: 'PopupWindow',
    components: { IconComponent },
    props: {
        /**
         *   popup
         * @type {Array<any>}
         */
        action: Array,
        /**
         *   popup 
         * @type {Object}
         */
        target: Object
    },
    data() {
        return {
            /**
            *  popup
            * @type {boolean}
            */
            isOpen: false
        };
    },
    methods: {
        /**
           *    
           * @param {any} action
           */
        Action(action) {
            console.log(action);
            this.isOpen = false;
        }
    }
});
