import Vue from 'vue';
import { SettingsIcon, MenuIcon, AlertTriangleIcon, ChevronDownIcon, ListIcon, UserIcon, AtSignIcon, InfoIcon, DiscIcon, CheckIcon, XIcon, HomeIcon, SearchIcon, LayersIcon } from 'vue-feather-icons';
export default Vue.extend({
    name: 'Icon',
    components: {
        SettingsIcon, MenuIcon, AlertTriangleIcon, ChevronDownIcon, ListIcon,
        UserIcon, AtSignIcon, InfoIcon, DiscIcon, CheckIcon, XIcon, HomeIcon,
        SearchIcon, LayersIcon
    },
    props: {
        /**
        *  
        * @type {string}
        */
        iconName: {
            required: true,
            type: String,
        },
        /**
        *  
        * @type {string}
        */
        iconSize: {
            required: true,
            type: String,
        },
    },
});
