import Vue from 'vue';
export default Vue.extend({
    name: 'ButtonComponent',
    props: {
        /**
        *  
        * @type {Object<ButtonModel>}
        */
        component: {
            required: true,
            type: Object
        }
    },
    /**
    *    
    * @param {string} url
    */
    methods: {
        Action(url) {
            this.$router.push(url);
        }
    }
});
