import Vue from 'vue';
import PopupWindow from '../PopupWindow.vue';
import IconComponent from '../IconComponent.vue';
export default Vue.extend({
    name: 'TableComponent',
    components: {
        PopupWindow, IconComponent
    },
    props: {
        /**
        *  
        * @type {Object<TableModel>}
        */
        component: {
            required: true,
            type: Object
        }
    },
    data() {
        return {
            /**
            *    
            * @type {string}
            */
            search: ''
        };
    },
    computed: {
        /**
        *   ,     
        * @return {Array<any>}
        */
        filteredTable() {
            if (this.component.searchKey) {
                const key = this.component.searchKey;
                return this.component.data.filter(search => {
                    return search[key].toLowerCase().match(this.search.toLowerCase());
                });
            }
            return this.component.data;
        }
    }
});
